import React, { Component } from "react";
import axios from "axios";
import { starshipsUrl } from "../../config";
import StarShip from "./StarShip";
import Header from "../Header";

class StarShipsList extends Component {
  state = {
    shipInfo: []
  };

  componentDidMount() {
    const getData = async () => {
      try {
        const res = await axios.get(starshipsUrl);
        this.setState({
          shipInfo: res.data.results
        });
        console.log(this.state.shipInfo[0]);
      } catch (error) {
        console.log(error);
      }
    };
    return getData();
  }
  render() {
    return (
      <>
        <Header />
        <div>
          {this.state.shipInfo.map((info, index) => {
            <StarShip name={index[info]} />;
          })}
        </div>
      </>
    );
  }
}

export default StarShipsList;
